"use client";

import { useRef, useEffect } from "react";

interface MobileMenuProps {
  isOpen: boolean;
  onClose: () => void;
}

const MobileMenu: React.FC<MobileMenuProps> = ({ isOpen, onClose }) => {
  const navLinksRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (isOpen && navLinksRef.current) {
      navLinksRef.current.style.transition =
        "transform 1s ease-in-out, opacity 0.3s ease-in-out";
      navLinksRef.current.style.transform = "translateY(0)";
      navLinksRef.current.style.opacity = "1";
    } else if (navLinksRef.current) {
      navLinksRef.current.style.transition =
        "transform 0.3s ease-in-out, opacity 0.3s ease-in-out";
      navLinksRef.current.style.transform = "translateY(-100%)";
      navLinksRef.current.style.opacity = "0";
    }
  }, [isOpen]);

  return (
    <div
      ref={navLinksRef}
      className={`w-full transition-transform duration-300 ease-in-out transform ${
        isOpen ? "translate-y-0 opacity-1" : "-translate-y-full opacity-0"
      } absolute top-18 left-0 bg-blue-400 z-0 md:hidden`} // Changed z-0 to z-1
    >
      <ul className="z-10 font-medium flex flex-col p-4 mt-4 text-black">
        <li
          className={`transition-opacity duration-300 ease-in-out ${
            isOpen ? "opacity-1 delay-100" : "opacity-0"
          }`}
        >
          <a
            href="/"
            className="block py-2 px-3 text-black"
            aria-current="page"
          >
            Instagram
          </a>
        </li>
        <li
          className={`transition-opacity duration-300 ease-in-out ${
            isOpen ? "opacity-1 delay-200" : "opacity-0"
          }`}
        >
          <a href="/youtube" className="block py-2 px-3 text-black">
            YouTube
          </a>
        </li>
        <li
          className={`transition-opacity duration-300 ease-in-out ${
            isOpen ? "opacity-1 delay-300" : "opacity-0"
          }`}
        >
          <a href="/tiktok" className="block py-2 px-3 text-black">
            TikTok
          </a>
        </li>
      </ul>
    </div>
  );
};

export default MobileMenu;
